@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.info__pokemon-stats {
  width: 100%;
  display: flex;
  height: 250px;
  justify-content: center;
  align-items: center;
}

.info__pokemon-stats > .pokemon-stats-left,
.info__pokemon-stats .pokemon-stats-right {
  padding: 5px;
  height: 240px;
}

.info__pokemon-stats > .pokemon-stats-left > .pokemon-stats-title,
.info__pokemon-stats .pokemon-stats-right > .pokemon-stats-title {
  padding: 0px 5px;
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 600;
  text-align: center;
  position: relative;
  color: #dfdfdf;
  display: block;
  border-bottom: 1px solid #2c5364;
}

.pokemon-stats-left {
  width: 100%;
  height: 100%;
}

.pokemon-stats-left > .pokemon-stat {
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 20px;
  margin-top: 0.625rem;
}

.pokemon-stats-left > .pokemon-stat > h5 {
  font-weight: 600;
  font-size: 0.75rem;
  text-transform: uppercase;
  margin-left: 5px;
  margin-bottom: 5px;
  color: #dfdfdf;
}

.pokemon-stats-left > .pokemon-stat > .progress {
  width: 100%;
  height: 0.75rem;
  background: #1c1f25;
  border-radius: 2px;
}

.pokemon-stats-left > .pokemon-stat > .progress > .progress-bar {
  height: 100%;
  width: 0%;
  background: linear-gradient(90deg, #0084ac 0%, #53d7ff 100%);
  border-radius: 2px;
  position: relative;
  /* transition: all 1.5s; */
}

.pokemon-stats-left > .pokemon-stat > .progress > .progress-bar > div {
  position: absolute;
  right: 5%;
  color: #242424;
  font-weight: 600;
  font-size: 10px;
}

.pokemon-stats-right {
  width: 100%;
  height: 100%;
}

.card-pokemon {
  position: relative;
  width: 100%;
  height: 90%;
  display: flex;
  text-align: center;
  align-items: center;
  flex-direction: column;
}

.card-pokemon > .card-pokemon-number {
  padding: 10px 30px;
  text-transform: capitalize;
  font-weight: 600;
  background: #00b4db;
  color: #dfdfdf;
  background: linear-gradient(to right, #00b4db, #0083b0);
  font-size: 1.2rem;
  line-height: 1.2rem;
  top: 0;
  border-radius: 5px;
}

.card-pokemon > .info__pokemon-name {
  font-size: 3rem;
  font-weight: 600;
  text-transform: uppercase;
  color: #f5f3f3;
  margin: 0;
}

.card-pokemon > .pokemon-container-img {
  width: 100%;
  height: 350px;
  display: flex;
  justify-content: center;
  position: relative;
  padding: 10px;
}

.card-pokemon > .pokemon-container-img > .pokemon-img {
  z-index: 1;
  max-width: 350px;
  max-height: 350px;
  min-height: 300px;
  min-height: 300px;
  -webkit-filter: drop-shadow(0 0 30px #0f0c29d0);
          filter: drop-shadow(0 0 30px #0f0c29d0);
}

.card-pokemon > .info__general {
  display: flex;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  width: 100%;
  position: relative;
}

.card-pokemon > .info__general > .info__pokemon-type {
  width: 100%;
  display: flex;
  color: #dfdfdf;
  font-weight: 600;
  justify-content: center;
}

.card-pokemon > .info__general > .info__pokemon-type > img {
  width: 48px;
  height: 48px;
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
  -webkit-filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.5));
}

.card-pokemon > .info__general > .info__pokemon_size {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: -5px;
  width: 100%;
  color: #dfdfdf;
}

.card-pokemon > .info__general > .info__pokemon_size > .info__pokemon-exp {
  margin-left: 20px;
  font-size: 1rem;
  font-weight: 600;
  text-transform: uppercase;
  color: #aaa9a9;
}

.card-pokemon > .info__general > .info__pokemon_size > p {
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 600;
  display: flex;
  flex-direction: column-reverse;
}

.card-pokemon > .info__general > .info__pokemon_size > p > span {
  font-size: 14px;
  text-transform: lowercase;
}

.input-search {
  margin-bottom: 8%;
  width: 70%;
}

@media screen and (max-width: 768px) {
  .input-search {
    margin-top: 5%;
  }
}

.input-search > input[type="text"] {
  background: transparent;
  color: #fff;
  font-weight: 600;
}

.input-search > input[type="text"]:focus {
  box-shadow: none;
  border-color: #fff;
}

@-webkit-keyframes imgAnimate {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@keyframes imgAnimate {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.container-fluid > .row > .bar-menu-inferior {
  position: fixed;
  z-index: 2;
  bottom: 0;
  width: 100%;
  height: 50px;
  background: transparent;
  display: flex;
  justify-content: center;
  -webkit-transform: translateY(-65px);
          transform: translateY(-65px);
}

.container-fluid > .row > .bar-menu-inferior > button {
  background: #0a0a0a;
  border-radius: 50%;
  width: 190px;
  height: 190px;
  padding: 5px;
  border: none;
  -webkit-animation: rotate 5s infinite linear;
          animation: rotate 5s infinite linear;
}

.container-fluid > .row > .bar-menu-inferior > button:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

.container-fluid > .row > .bar-menu-inferior > button > img {
  width: 100%;
  height: 100%;
  transition: 0.3s all;
}

.container-fluid > .row > .bar-menu-inferior > button > img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.container-fluid > .row > .bar-menu-inferior > button > img:active {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@media screen and (max-width: 768px) {
  .container-fluid > .row > .bar-menu-inferior > button {
    width: 100px;
    height: 100px;
  }

  .container-fluid > .row > .bar-menu-inferior {
    -webkit-transform: translateY(-45%);
            transform: translateY(-45%);
  }
}

@media screen and (max-width: 968px) {
  .container-fluid > .row > .bar-menu-inferior > button {
    width: 150px;
    height: 150px;
  }
}

.pokemon-moves__ability,
.pokemon-moves__moves {
  overflow: hidden;
}

.pokemon-moves__ability > .pokemon-special-move,
.pokemon-moves__moves > .pokemon-special-move {
  color: #dfdfdf;
  font-weight: 600;
  font-size: 0.9rem;
  margin: 5px 0;
  background: #e35d5b;
  padding: 5px;
  width: 100%;
  text-transform: capitalize;
  text-align: center;
}

.pokemon-moves__ability > .pokemon-special-move.ability,
.pokemon-moves__moves > .pokemon-special-move.ability {
  background: radial-gradient(circle, #272b35 0%, #0f2027 100%);
  text-align: center;
}

.pokemon-moves__ability {
  width: 100%;
  transition: 0.5s ease height;
}

.pokemon-moves__moves {
  display: flex;
  flex-direction: column;
  height: 120px;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Roboto Slab", serif;
}

:root {
  --widthImg: 300px;
  /* --url: '../img/bgBall.jpg'; */
  --url: "../../images/bgBall.jpg";
}

.container-fluid {
  background-image: linear-gradient(
      to bottom,
      rgba(36, 36, 62, 0.37),
      rgba(48, 43, 99, 0.349),
      rgba(15, 12, 41, 0.322)
    ),
    url(/pokedex-react/static/media/bgBall.2cd384db.jpg);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  width: 100vw;
  justify-content: center;
  height: 100vh;
  display: flex;
  transition: 0.3s all;
  padding: 0 0;
}

.container-fluid > .row {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
}

.container-fluid > .row > .col-left,
.container-fluid > .row .col-middle,
.container-fluid > .row .col-right {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.container-fluid > .row > .col-left {
  position: relative;
  text-align: center;
}

.container-fluid > .row > .col-middle {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
}

.pokemon-stats-title {
  padding: 0px 5px;
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 600;
  text-align: center;
  position: relative;
  color: #dfdfdf;
  border-bottom: 1px solid #2c5364;
}

@media (max-width: 576px) {
  .container-fluid {
    width: 100%;
    height: 100%;
  }
  .row {
    min-width: 100%;
  }
  .col-left {
    order: 2;
  }
  .col-middle {
    order: 1;
  }
  .col-right {
    order: 3;
    margin-bottom: 20px !important;
    margin-top: 50px !important;
    padding-bottom: 50px !important;
  }
  .info__pokemon-name {
    font-size: 2.5rem !important;
  }
  .pokemon-container-img {
    height: 325px !important;
  }
  .pokemon-img {
    max-width: 300px !important;
  }
  .button-generate {
    margin-top: 20px;
    position: relative !important;
  }
  .pokemon-stats-left,
  .pokemon-stats-right {
    min-width: 90% !important;
  }
}

@media (max-width: 380px) {
  .container-fluid {
    width: 100% !important;
    height: 100% !important;
  }
  .row {
    min-width: 100%;
  }
  .col-left {
    order: 2;
  }
  .col-middle {
    order: 1;
  }
  .col-right {
    order: 3;
    margin-bottom: 20px !important;
    margin-top: 30px;
    padding-bottom: 40px;
  }
  .pokemon-container-img {
    height: 335px !important;
  }
  .button-generate {
    position: relative !important;
  }
}

@media (max-width: 768px) {
  .button-generate {
    position: relative !important;
  }
  .pokemon-container-img {
    height: 420px !important;
  }
  .card-pokemon-number {
    margin-top: 40px !important;
  }
  .pokemon-img {
    max-width: 300px !important;
  }
  .info__pokemon-name {
    font-size: 2.5rem !important;
    margin: 10px 0 0 0 !important;
  }
}

@media (max-width: 994px) {
  .button-generate {
    position: relative !important;
  }
  .info__pokemon-name {
    font-size: 2.5rem !important;
  }
  .info__general {
    margin-bottom: 30px !important;
  }
}

